import * as React from 'react'
import Button from 'src/packages/button/Button'
import Code from 'src/components/Code'
import CodeBox from 'src/components/CodeBox'
import { renderString } from 'src/utils/renderCode'

class Start extends React.Component {
  public static displayName = 'Start'

  public render() {
    return (
      <div className="start">
        <section>
          <h1>开始使用</h1>
          <p className="text">
            Xue-react 目前正在编写中，尚未构建发布，不过你也可以克隆
            <a
              href="https://github.com/BlameDeng/xue-react"
              target="_blank"
              rel="noopener noreferrer"
              className="link"
            >
              官方仓库
            </a>
            使用源码来尝尝鲜。
          </p>
        </section>
        <section>
          <h2>配置 Tslint</h2>
          <p className="text">
            Tslint
            过于严格的语法检查并不利于普通开发，因此需要做一些个性化配置，Xue-react
            的相关 Tslint 配置可参考官方仓库 tslint.json 文件。
          </p>
        </section>
        <section>
          <h2>配置 Scss</h2>
          <p className="text">
            Xue-react 使用了 Scss 来编写样式，因此需要您自己配置 Scss 环境。
          </p>
        </section>
        <section>
          <h2>简单示例</h2>
        </section>
        <div className="example">
          <div className="container">
            <Button style={{ marginRight: '20px' }} type="primary">
              Primary
            </Button>
          </div>
          <CodeBox
            title="使用 Button"
            description="使用 Xue-ui 按钮组件。"
            defaultVisible={true}
          >
            {renderString(`import Button from 'components/Button'`)}
            <br />
            <Code
              code={`&lt;Button type="primary"&gt;Primary&lt;/Button&gt;`}
            />
          </CodeBox>
        </div>
      </div>
    )
  }
}

export default Start



